.button {
  min-width: 100px;
  min-height: 36px;
  padding: 6px 8px;
  border: 1px solid transparent;
  border-radius: 4px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  font-size: 1.6rem;
  font-weight: 700;
  font-family: inherit;
  line-height: 2.1rem;
  letter-spacing: 0.03px;
  user-select: none;
  cursor: pointer;
  gap: 8px;

  &.button-block {
    display: flex;
    width: 100%;
  }

  & + .button {
    margin-left: 16px;
  }

  // Button type
  &.primary {
    color: white;
    background-color: var(--primary);

    &:hover {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #fe2c55;
    }

    &.outline {
      color: var(--primary);
      border-color: currentColor;
      background-color: transparent;

      &:hover {
        background-color: rgba(254, 44, 85, 0.08);
      }
    }
  }

  &.secondary {
    color: black;
    background-color: var(--secondary);

    &:hover {
      background-color: rgba(22, 24, 35, 0.34);
    }

    &.outline {
      border-color: var(--secondary);
      background-color: transparent;

      &:hover {
        background-color: rgba(22, 24, 35, 0.03);
      }
    }
  }

  &.plain {
    min-width: 0 !important;
    width: fit-content;
  }

  // Button size
  &.small {
    min-width: 96px;
    padding: 6px 16px;
    font-weight: 500;
  }

  &.large {
    min-width: 168px;
    min-height: 48px;
  }

  // Rounded Button
  &.rounded-none {
    border-radius: 0;
  }
  &.rounded-pill {
    border-radius: 52px;
  }
  &.rounded-2 {
    border-radius: 2px;
  }

  &.disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.icon {
  width: 24px;
  text-align: center;
}

